<template>
  <div class="switch-btn">
    <input type="checkbox" :name="inputName" :id="inputId" v-model="innerValue" class="d-none">
    <label :for="inputId">
      <div class="switch-btn-track"></div>
      <div class="switch-btn-cap"></div>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'SwitchBtn',
    props: {
      inputName: String,
      inputId: {
        type: [String, Array, Object],
        default: 'switch-btn',
        required: true
      },
      inputValue: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      innerValue: {
        get () {
          return this.inputValue;
        },
        set (val) {
          console.log(val);
          this.$emit('update:inputValue', val)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/base";

  label {
    margin: 0;
    position: relative;
  }
  input {
    &:checked {
      + label {
        .switch-btn {
          &-track {
            background-color: $primary;
          }
          &-cap {
            transform: translate(75%, -50%);
          }
        }
      }
    }
  }
  .switch-btn {
    padding: 10px 15px;
    &-track {
      width: 25px;
      height: 14px;
      background-color: $gray-500;
      border-radius: 10px;
    }
    &-cap {
      width: 20px;
      height: 20px;
      background-color: white;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(-50%, -50%);
      transition: transform .3s ease;
      box-shadow: 0 2px 3px -1px rgba(black, .3), 0 2px 5px 1px rgba(black, .2);
      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba($primary, .1);
        border-radius: 50%;
        transition: transform .3s ease;
        transform: scale(0);
        z-index: 1;
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: white;
        border-radius: 50%;
        z-index: 2;
      }
    }
    &:hover, :focus {
      .switch-btn {
        &-cap {
          &:before {
            transform: scale(2);
          }
        }
      }
    }
  }
</style>
