<script>
  import IconBox from "../components/IconBox";
  import Avatar from "../components/Avatar";
  import IconRound from "../components/IconRound";
  import SwitchBtn from "../components/SwitchBtn";
  import { slideShow } from "../helper/extends";

  export default {
    name: 'OrderList',
    extends: slideShow,
    components: {SwitchBtn, IconRound, Avatar, IconBox},
    data() {
      return {
        orderStep: [
          {
            title: '購買明細',
            active: true,
            complete: false
          },
          {
            title:'訂購資訊',
            active: false,
            complete: false
          },
          {
            title: '送單完成',
            active: false,
            complete: false
          }
        ],
        moneyIcon: {
          wrap: {
            color: 'white',
            backgroundColor: '#FBCB3D'
          },
          icon: ['fas', 'fa-dollar-sign', 'fa-fw']
        },
        iconBoxes: [
          {
            text: '取餐',
            style: {
              backgroundImage: `url(${require('../assets/img/shopping.png')})`
            }
          },
          {
            text: '揪團',
            style: {
              backgroundImage: `url(${require('../assets/img/teamwork.png')})`
            }
          },
        ],
        shipping: {
          type: {
            title: '自取',
            dire: '需到店取貨！請確認門市正確，再進行下單',
            class: ['far', 'fa-hand-paper'],
          },
          time: {
            title: '15:50',
            dire: '取餐時間可能會依門市現場狀況有所調整',
            class: ['far', 'fa-clock']
          }
        },
        orderInfo: {
          user: {
            title: 'Sabrina Yu',
            sub: '0988100761',
            dire: '留言',
            class: [],
            isEdit: true
          },
          cash: {
            title: '現金',
            sub: '紙本發票',
            dire: '無統一發票',
            class: ['fas', 'fa-money-bill', 'fa-fw'],
            isEdit: true
          },
          coupon: {
            title: '折價卷',
            sub: '',
            dire: '無可用折價券',
            class: ['fas', 'fa-vote-yea', 'fa-fw']
          },
          bag: {
            title: '塑膠袋',
            sub: '',
            dire: '依據政府塑膠袋政策，即日起此門市將收取相關費用',
            class: ['fas', 'fa-shopping-bag', 'fa-fw'],
            switch: {
              value: false
            }
          },
        },
        buttons: [
          {
            text: '繼續加購',
            disable: true,
            class: {
              'btn-outline-secondary': true
            },
            fn: 'prev'
          },
          {
            text: '下一步',
            disable: false,
            class: {
              'text-white': true,
              'btn-primary': true
            },
            fn: 'next'
          }
        ],
        checkOrder: [
          {
            title: '訂單編號',
            content: 'M0004792281'
          },
          {
            title: '訂購門市',
            content: '50嵐 捷運善導寺店'
          },
          {
            title: '門市電話',
            content: '02-2351-8482'
          },
          {
            title: '取餐方式',
            content: '自取'
          },
          {
            title: '取餐時間',
            content: '18:00'
          }
        ]
      }
    },
    methods: {
      handelClick(target) {
        let current, index;
        let max = this.orderStep.length - 1;
        let fn = target.fn;

        if (fn === 'next') {
          current = this.slideCount + 1;
          index = this.slideCount;

          if (current >= max) {
            current = max;
            index = max - 1;
            this.orderStep[current].complete = true;
          }

          this.orderStep[index].complete = true;
          this.orderStep[current].active = true;

          this.nextSlide();
        } else if (fn === 'prev') {
          index = this.slideCount;
          current = this.slideCount - 1;

          if (current <= 0) {
            current = 0;
            index = 0 + 1;
          }
          this.orderStep[current].complete = false;
          this.orderStep[index].active = false;
          this.orderStep[index].complete = false;

          this.prevSlide();
        }

        this.changeBtnStates(fn, current)
      },
      changeBtnStates(fn, current = 0) {
        if (fn === 'prev' && current === 0) {
          this.$router.push({path: '/menu'});
        }
        switch (current) {
          case '0':
            this.buttons[0].text = '繼續加購';
            this.buttons[1].text = '下一步';
            break;
          case '1':
            this.buttons[0].text = '上一步';
            this.buttons[1].text = '完成訂購';
            break;
        }
      }
    },
    created() {
      this.slideInit(this.orderStep.length);
    }
  }
</script>

